.card {
    border: none !important;
    z-index: 2;
}

.cardText {
    color: #51BFAD;
    font-size: 45px;
    font-weight: 500;
}

.cardText p {
    font-size: 15px;
}

.list {
    flex: 1 1 auto;
    padding: 20px;
}

.listItem {
    border: none !important;
    color: #303A6C;
    font-size: 20px;
}

.listItem span {
    color: #F69C5D;
}

.join {
    background-color: #51BFAD;
    border-color: #51BFAD;
    font-size: 18px;
    font-weight: 500;

}

@media (max-width:786px) {
    .card {
        margin: auto;
    }
}